@font-face {
  font-family: "Proximus-Bold";
  src: url("../fonts/Proximus-Bold.woff") format('woff');
}

@font-face {
  font-family: "Proximus-Regular";
  src: url("../fonts/Proximus-Regular.woff") format('woff');
  font-weight: bold;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #fff;
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 12px;
}

* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

h1 {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 44px;
  color: rgb(92, 45, 145);
}

h2 {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 26px;
  color: rgb(0, 164, 193);
}

h3 {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 14px;
  color: rgb(92, 45, 145);
}

h4 {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 24px;
  color: rgb(92, 45, 145);
}

h5 {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 18px;
  color: rgb(92, 45, 145);
  margin: 0;
  margin-bottom: 0.5cm;
}

h6 {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 18px;
  color: rgb(92, 45, 145);
  margin: 0;
}

table, tr, td {
  vertical-align: top;
}

table {
  width: 100%;
}

div.subpage.index > h1 {
  margin-top: 0cm;
  margin-bottom: 0.3cm;
}

div.subpage.index > h2 {
  margin-top: 15.5cm;
  margin-bottom: 0.5cm;

}

div.subpage.index > h3 {
  margin-top: 0cm;
  margin-bottom: 0cm;

}

ul.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

ul.address {
  float: left;
  width: 50%;
}

.page {
  width: 210mm;
  min-height: 297mm;
  padding: 20mm;
  margin: 10mm auto;
  border: 1px #D3D3D3 solid;
  border-radius: 5px;
  background: white;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.page.index {
  background-image: url('../img/page_bg_index.png') !important;
  background-repeat: no-repeat !important;
  background-position: left !important;

}

.subpage {
  /*padding: 1cm;*/
  padding-top: 1cm;
  padding-bottom: 1cm;
  /*border: 1px dotted grey;*/
  height: 257mm;
  /*outline: 2cm #fff solid;*/
}

.header {
  /*border: 1px solid red;*/
  position: absolute;
  margin-top: -1cm;
  height: 2cm;
  width: 170mm;
  background-image: url('../img/header_logo_proximus.png');
  background-repeat: no-repeat;
}

.header.index {
  background-image: none;
}

.headerTitle {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 12px;
  float: right;
  padding-top: 1cm;
}

.footer {
  /*border: 1px solid red;*/
  position: absolute;
  margin-top: -1cm;
  height: 2cm;
  width: 170mm;
  background-image: none;
}

.footer.index {
  background-image: url('../img/header_logo_proximus.png');
  background-repeat: no-repeat;
  background-position: right;
}

.footerTitle {
  font-family: "Proximus-Bold", "Courier New", Courier, monospace;
  font-size: 12px;
}

.footerText {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 9px;
  float: left;
}

.footerPagenumber {
  font-family: "Proximus-Regular", "Courier New", Courier, monospace;
  font-size: 9px;
  float: right;
}

@page {
  size: A4;
  margin: 0;
}

@media print {
  html, body {
    width: 210mm;
    height: 297mm;
  }
  .page {
    margin: 0;
    border: initial;
    border-radius: initial;
    width: initial;
    min-height: initial;
    box-shadow: initial;
    background: initial;
    page-break-after: always;
  }
}